.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  background-color: #84b9ff;
}

.background {
  position: absolute;
  left:0;
  top:0;
  width: 100vw;
  height: 100vh;
  background-image: url(https://images.unsplash.com/photo-1522881451255-f59ad836fdfb?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1138&q=80);
  background-size: cover;
  opacity: 0.5;
  filter: blur(5px);
  transform: scale(1.1);
}

.input {
  text-align: left;
  margin-top: 30px;
  width: 70vw;
  min-width: 430px;
  height: 78vh;
  max-height: 500px;
  min-height: 400px;
  box-shadow: 2px 2px 5px 1px #1b1b1b7a;
}